import axios from "axios";
import React, { useEffect, useState } from "react";

const serviceKey =
  "eRLOwH19Z%2FYOTC00zcefvpxvwseGUrgSsocmGQ0SiDLenLDTZtVZRYaMYwScpSNCHiQBLDrbYbFFDkyqu%2FdjcQ%3D%3D";
const baseUrl = "https://apis.data.go.kr/B551011/KorService1";
const pageNo = 1;
const numOfRows = 20000;

const Test = () => {
  const [combinedData, setCombinedData] = useState([]);
  const [randomSpot, setRandomSpot] = useState();

  let data1 = [];
  let data2 = [];

  const testApi = () => {
    //자연관광지
    axios
      .get(
        `${baseUrl}/areaBasedList1?serviceKey=${serviceKey}&pageNo=${pageNo}&numOfRows=${numOfRows}&MobileApp=AppTest&MobileOS=ETC&arrange=O&contentTypeId=12&cat1=A01&cat2=A0101&_type=json`
      )
      .then((res) => {
        // 성공한 경우 실행
        const itemList = res.data.response.body.items.item;
        data1 = itemList;
        setCombinedData([...data1, ...data2]);
      })
      .catch((err) => {
        // 에러인 경우 실행
        console.log("====================================");
        console.log("에러", err);
        console.log("====================================");
      });

    //휴양관광지
    axios
      .get(
        `${baseUrl}/areaBasedList1?serviceKey=${serviceKey}&pageNo=${pageNo}&numOfRows=${numOfRows}&MobileApp=AppTest&MobileOS=ETC&arrange=O&contentTypeId=12&cat1=A02&cat2=A0202&_type=json`
      )
      .then((res) => {
        // 성공한 경우 실행
        const itemList = res.data.response.body.items.item;
        data2 = itemList;
        setCombinedData([...data1, ...data2]);
      })
      .catch((err) => {
        // 에러인 경우 실행
        console.log("====================================");
        console.log("에러", err);
        console.log("====================================");
      });
  };

  useEffect(() => {
    console.log(combinedData);
  }, [combinedData]);

  useEffect(() => {
    console.log(randomSpot);
  }, [randomSpot]);

  const randomNumber = () => {
    return Math.floor(Math.random() * (combinedData.length + 1));
  };

  const makeRandom = (randomIdx) => {
    const tempSpot = combinedData[randomIdx];

    if (tempSpot.firstimage.length === 0) {
      makeRandom(randomNumber());
    } else {
      setRandomSpot(tempSpot);
    }
  };

  return (
    <div>
      <div>-------------------------------</div>

      <button onClick={() => makeRandom(randomNumber())}>랜덤장소선정</button>
      <div>{JSON.stringify(randomSpot)}</div>
      <img src={randomSpot?.firstimage2} />

      <div>-------------------------------</div>

      <button onClick={testApi}>테스트</button>
      <div>-------------------------------</div>
      {/* <div>{JSON.stringify(combinedData)}</div> */}
      <div>-------------------------------</div>
      <div>-------------------------------</div>
      <div>-------------------------------</div>
    </div>
  );
};

export default Test;
